/* global angular, ScrollReveal */
angular.module('ua5App')
    .directive('scrollReveal', ['$timeout', function($timeout) {
        return {
            restrict: 'A',
            scope: {
                scrollRevealSettings: '='
            },
            link: function($scope, element, attrs) {
                $timeout(function() {
                    if (!window.scrollReveal) {
                        window.scrollReveal = new ScrollReveal();
                    }
                    window.scrollReveal.reveal(element, $scope.scrollRevealSettings);
                }, 0);
            }
        };
    }])
;

/* global angular */
angular.module('ua5App')
    .directive('shareE', ['$location', function($location) {
        return {
            restrict: 'A',
            scope: {
                shareEBody: '@',
                shareESubject: '@'
            },
            link: function($scope, element, attrs) {
                var body;
                var subject;
                body = $scope.shareEBody || $location.absUrl();
                subject = $scope.shareESubject || $location.absUrl();
                element[0].href = 'mailto:?subject=' + subject + '&body=' + body;
                element[0].target = '_blank';
            }
        };
    }])
;

/* global angular */
angular.module('ua5App')
    .directive('shareFb', ['popup', '$location', function(popup, $location) {
        return {
            restrict: 'A',
            scope: {
                shareFbUrl: '@'
            },
            link: function($scope, element, attrs) {
                var url;
                var fbUrl;
                url = $scope.shareFbUrl || $location.absUrl();
                fbUrl = 'https://www.facebook.com/sharer.php?u=' + encodeURIComponent(url);
                element.on('click', function() {
                    popup.show(fbUrl);
                });
                $scope.$on('$destroy', function() {
                    element.off('click');
                });
            }
        };
    }])
;

/* global angular */
angular.module('ua5App')
    .directive('shareTw', ['popup', '$location', function(popup, $location) {
        return {
            restrict: 'A',
            scope: {
                shareTwUrl: '@',
                shareTwText: '@'
            },
            link: function($scope, element, attrs) {
                var text;
                var twUrl;
                var url;
                url = $scope.shareTwUrl || $location.absUrl();
                text = $scope.shareTwText || '';
                twUrl = 'https://twitter.com/intent/tweet?text=' + text + '&url=' + url;
                element.on('click', function() {
                    popup.show(twUrl, 600, 253);
                });
                $scope.$on('$destroy', function() {
                    element.off('click');
                });
            }
        };
    }])
;

